<!-- Your HTML goes here -->
<template>
  <div>
    <h1>{{title}}</h1>
    <svg :class="`line-svg`" :viewBox="viewBox">

        <!-- Chart and Axes -->
        <g :transform="`translate(${margin.left}, ${margin.top})`">
            <path class='line-chart1'></path>
            <g class="plot"></g>
            <g class="x-axis" :transform="`translate(0, ${height})`"></g>
            <g class="y-axis"></g>
        </g>

        <!-- Axis Labels -->
        <g :transform="`translate(${margin.left + (width/2.5)}, ${margin.top + margin.bottom/1.5 + height})`">
            <text>{{labelX}}</text>
        </g>
        <g :transform="`translate(${margin.left/4}, ${margin.top + (height/1.8)}) rotate(-90)`">
            <text>{{labelY}}</text>
        </g>
    </svg>
  </div>
</template>

<script>
import * as d3 from "d3";
export default {
  name: "LineChart",
  props: {
    chartId: {
        required: true,
        type: String
    },
    title: {
        type: String
    },
    dataset: {
      required: true,
      type: Array,
    },
    dataset1: {
        required: true
    },
    dataset2: {
        required: false
    },
    dataset3: {
        required: false
    },
    dataset4: {
        required: false
    },
    dataset5: {
        required: false
    },
    attribX: {
      required: true,
      type: String,
    },
    labelX: {
        type: String
    },
    attribY: {
      required: true,
      type: String,
    },
    labelY: {
        type: String
    },
    attribColor: {
      required: false,
      type: String,
    },
    height: {
      required: true,
      type: Number,
    },
    width: {
      required: true,
      type: Number,
    },
  },
  data() {
    return {
      x: null,
      y: null,
      x1: null,
      y1: null,
      color: null,
      margin: {
          left: 75,
          top: 50,
          right: 20,
          bottom: 75,
      }
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      this.x = d3.scaleLinear();
      this.y = d3.scaleLinear();
      this.x1 = d3.scaleLinear().domain(d3.extent(this.dataset, d => d[this.attribX])).range([0, this.width]);
      this.y1 = d3.scaleLinear().domain(d3.extent(this.dataset, d => d[this.attribY])).range([this.height, 0]);
      this.color = d3.scaleOrdinal();
      this.update();
    },
    update() {
      this.x.domain(d3.extent(this.dataset, d => d[this.attribX])).range([0, this.width])
      this.y.domain(d3.extent(this.dataset, d => d[this.attribY])).range([this.height, 0])
      this.x1.domain(d3.extent(this.dataset, d => d[this.attribX])).range([0, this.width])
      this.y1.domain(d3.extent(this.dataset, d => d[this.attribY])).range([this.height, 0])
      this.color.domain(this.dataset, d => d[this.attribColor]).range(d3.schemeTableau10)
      this.renderLine();
      this.renderScatter();
      this.renderXAxis();
      this.renderYAxis();
    },
    renderLine() {
        let line = d3.line()
                .x(d => this.x1(d[this.attribX]))
                .y(d => this.y1(d[this.attribY]))

            d3.select('.line-chart1')
            .attr('d', line(this.dataset1))

    },
    renderScatter() {
      // Plot points
      d3.select(`.line-svg`)
        .select(".plot")
        .selectAll("circle")
        .data(this.dataset)
        .join("circle")
        .transition()
        .duration(200)
        .attr("cx", (d) => this.x(d[this.attribX]))
        .attr("cy", (d) => this.y(d[this.attribY]))
        .attr("fill", (d) => this.color(d[this.attribColor]))
  
      // Add mouse events
      d3.select(`.line-svg`)
        .select(".plot")
        .selectAll("rect")
        .on('click', (event, d) => {
            this.itemSelected(d)
        })        
        .on('mouseover', function() {
            d3.select(this).attr('class', 'hover').attr('height', 15).attr('width', 15)
        })
        .on('mouseout', function() {
            d3.select(this).attr('class', null).attr('height', 10).attr('width', 10)
        })
      d3.select(`.line-svg`)
        .select(".plot")
        .selectAll("circle")
        .on('click', (event, d) => {
            this.itemSelected(d)
        })
        .on('mouseover', function(d) {
            d3.select(this).attr('class', 'hover')
            this.itemSelected(d)
        })
        .on('mouseout', function() {
            d3.select(this).attr('class', null)
            this.itemSelected(0)
        })
        .append("title")
        .text(function (d) {
          return (

            "User:\n"
            + d.user // We will have to use the get user api on the filtered top five datasets
            
            // + other descriptions
          );
        });
    },
    renderXAxis() {
        let xAxis = d3.axisBottom(this.x)
        d3.select(`.line-svg`)
          .select('.x-axis')
          .call(xAxis)
    },
    renderYAxis() {
        let yAxis = d3.axisLeft(this.y)
        d3.select(`.line-svg`)
          .select('.y-axis')
          .call(yAxis)
    },
    itemSelected(d) {
        console.log(`Selected ${d}`)
        this.$emit('item-selected', d)
    }
  },
  computed: {

    viewBox() {
      return `0 0 ${this.width+this.margin.left+this.margin.right} ${this.height+this.margin.top+this.margin.bottom}`
    }
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.plot rect {
    fill-opacity: 0.5;
}
.plot rect.hover {
    fill-opacity: 1;
    stroke: black;
}
.plot circle {
    fill-opacity: 0.75;
    r: 5;
}
.plot circle.hover {
    fill-opacity: 1;
    r: 7.5;
    stroke: black;
}

.line-svg {
    padding: 20px;
    background-color: #f0f0f0;
}

.line-svg .line-chart {
    stroke: #c72a2a;
    fill: none
}
</style>